import React from "react";
import {
  Page,
  Card,
  Row,
  Col,
  Fab,
  Icon,
  Navbar,
  Link,
  Button,
  CardContent,
  CardFooter,
  CardHeader,
  FabButtons,
  FabButton,
  Block,
  Subnavbar,
} from "framework7-react";

import { dict } from "../../Dict";


const RoomIndex = (props) => {
  function videos() {
    var result = [];

    if (props.feeds) {
      {
        props.feeds.map((feed) => {
          result.push(
            <div class="col-100 tablet-33" key={"container-" + feed.id}>
              <video
                controls={true}
                key={feed.id}
                id={"video-" + feed.id}
                src=""
                width="160"
                height="120"
                autoPlay
                playsInline
                muted={props.muted}
              />
            </div>
          );
          props.streamAttacher(feed)
        });
      }
    }
    return result;
  }
  return (
    <Page>
      <Navbar title={dict.profiles} backLink={dict.back}>
        <Link panelOpen="right">
          <Icon f7="bars"></Icon>
        </Link>
      </Navbar>
      <Subnavbar className="bg-black subnavbar-black">
        <div></div>
        <div className=''>
        <a onClick={() => props.publishCamera()}><i class="fa fa-video-camera ml-4" aria-hidden="true"></i></a>
        <a onClick={() => props.publishMicrophone()}><i class="fa fa-microphone ml-4" aria-hidden="true"></i></a>
        </div>

      </Subnavbar>
      <Block >
        <Row className="center" id="hosts">
          {videos()}
        </Row>
      </Block>

      <Block strong>

      </Block>

    </Page>
  );
};
export default RoomIndex;
